.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 0 1 60px;
    min-height: 60px;
    padding: 0 1rem;
    background-image: linear-gradient(to left, #f73152,#e21299);
    top: 0;
    position: fixed;
    width: 100%;
    box-sizing: border-box;
    box-shadow: 0 2px 3px  #5c5c5c;
    z-index: 99;
}

.navbar__logo {
    display: block;
    width: 80px;
}


@media (min-width: 992px) {
    .navbar {
        padding: 0 10%;
    }
}
.menu__button{
    cursor: pointer;
    text-transform: uppercase;
    padding: 0.5rem;
    border: 1px solid #ffffff;
    border-radius: 5px;
    color:#fff;
}

.menu__button--open:after {
    content: "";
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
}

.menu__options {
    list-style: none;
    color: white;
    font-weight: bold;
    font-size: 21px;
    font-family: monospace;
    text-transform: uppercase;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 80vw;
    padding: 2rem 1rem 0;
    margin: 0;
    background-image: linear-gradient(to left, #f73152,#e21299);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 1s ease-in-out;
    transition: transform 1s ease-in-out;
    transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

.menu__options--open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.menu__options--active {
    border-bottom: 2px solid white;
}

.menu__options  li + li {
    margin-top: 1rem;
}

.menu__options li a {
    cursor: pointer;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    font-family: 'Amatic SC', cursive;
    font-weight: 700;
}

@media (min-width: 992px) {
    .menu__button {
        display: none;
    }

    .menu__options {
        display: flex;
        align-items: center;
        position: static;
        position: initial;
        height: auto;
        height: initial;
        width: auto;
        width: initial;
        padding: 0;
        padding: initial;
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
        transition: none 0s ease 0s;
        transition: initial;
    }

    .menu__options--open {
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
    }

    .menu__options--active {
        border-color: #ffffff;
    }

    .menu__options li + li {
        margin-top: 0;
        margin-left: 1rem;
    }

    .menu__options li a {
        color: #fff;
        padding: 0.3rem 0;
    }
}

